


















import { Vue, Component, Prop } from 'vue-property-decorator';

interface CouponInterface {
	comment_content: string
	comment_star: number
	comment_user_phone: string
	coupon_endtime: string
	coupon_explain: string
	coupon_ico_path: string
	coupon_name: string
}

@Component({
	filters: {
		dateFormate (timestamp: string): string | number {
			const addZero = (value: number) => value >= 10 ? value : ('0' + value);
			const date = new Date(parseInt(timestamp, 10));
			return date.getFullYear() + '/' + addZero(date.getMonth()+1) + '/' + date.getDate();
		}
	}
})
export default class CouponBrief extends Vue {
	@Prop({type: Object}) private coupon!: CouponInterface;
}
